.container,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-xxl {
    width: 100%;
    padding: {
        right: 0.75rem;
        left: 0.75rem;
    }
    margin: {
        right: auto;
        left: auto;
    }
}

@include breakpoint(sm) {
    .container,
    .container-sm {
        max-width: 540px;
    }
}

@include breakpoint(md) {
    .container,
    .container-sm,
    .container-md {
        max-width: 720px;
    }
}

@include breakpoint(lg) {
    .container,
    .container-sm,
    .container-md,
    .container-lg {
        max-width: 960px;
    }
}

@include breakpoint(xl) {
    .container,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl {
        max-width: 1200px;
    }
}

@include breakpoint(xxl) {
    .container,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl,
    .container-xxl {
        max-width: 1320px;
    }
}

.columns {
    margin: {
        left: -0.75rem;
        right: -0.75rem;
        top: -0.75rem;
    }

    &.is-multiline {
        flex-wrap: wrap;
    }

    &.is-vcentered {
        align-items: center;
    }

    &:last-child {
        margin-bottom: -0.75rem;
    }
}

.column {
    display: block;
    flex-basis: 0;
    flex-grow: 1;
    flex-shrink: 1;
    padding: 0.75rem;
}

@include breakpoint(md) {
    .column.is-1 {
        flex: none;
        width: 8.33333%;
    }

    .column.is-2 {
        flex: none;
        width: 16.66667%;
    }

    .column.is-3 {
        flex: none;
        width: 25%;
    }

    .column.is-4 {
        flex: none;
        width: 33.33333337%;
    }

    .column.is-5 {
        flex: none;
        width: 41.66667%;
    }

    .column.is-6 {
        flex: none;
        width: 50%;
    }

    .column.is-7 {
        flex: none;
        width: 58.33333%;
    }

    .column.is-8 {
        flex: none;
        width: 66.66667%;
    }

    .column.is-9 {
        flex: none;
        width: 75%;
    }

    .column.is-10 {
        flex: none;
        width: 83.33333%;
    }

    .column.is-11 {
        flex: none;
        width: 91.66667%;
    }

    .column.is-12 {
        flex: none;
        width: 100%;
    }

    .column.is-full {
        flex: none;
        width: 100%;
    }

    .column.is-offset-1 {
        margin-left: 8.33333337%;
    }

    .is-mobile {
        display: flex;
    }

    .columns:not(.is-desktop) {
        display: flex;
    }
}
