/* Service Section
=====================================================================*/
.service-section {
    margin-top: 280px;
}

.section {
    padding: 3rem 1.5rem;
}

@include breakpoint(lg) {
    .section {
        padding: 3rem;
    }
}

/* Login Page
=====================================================================*/
.auth-wrapper {
    overflow: hidden;
    width: 100%;
    padding: 0;
    margin: 0;
}

.auth-wrapper.is-single {
    background: hsl(228deg 33% 97%);
    min-height: 100vh;
}

.auth-nav {
    position: absolute;
    top: 0;
    left: 0;
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.5rem;
}

.auth-nav .center {
    flex-grow: 2;
}

.auth-nav .center a {
    display: flex;
    justify-content: center;
    align-items: center;
}

.auth-nav .center a img {
    display: block;
    width: 100%;
    max-width: 40px;
}

.auth-wrapper .single-form-wrap {
    min-height: 690px;
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.auth-wrapper .single-form-wrap .inner-wrap {
    width: 100%;
    max-width: 400px;
    margin: 2.75rem auto 0;
}

.auth-wrapper .single-form-wrap .inner-wrap .form-card {
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 0.9rem;
    padding: 3.25rem;
    margin-bottom: 1rem;
}

