/*=====================================================================
Station:14 [ Footer ]
=====================================================================*/
.footer {
    position: relative;
    padding: 3rem 1.5rem 6rem;
    display: block;

    &.has-cta {
        margin-top: 6rem;
        padding-top: 10rem;
    }

    &.footer-dark {
        background: hsl(217deg 24% 19%);

        li a {
            color: hsl(232deg 14% 68%);
        }
    }

    .footer-column-list {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            padding: 0.25rem 0px;
        }

        li a {
            font-size: 1rem;
            font-weight: 400;
            color: hsl(229deg 11% 51%);
            transition: color 0.3s ease 0s;
            padding: 0.25rem 0px;
        }
    }
}

/*=====================================================================
Station:15 [ Accents ]
=====================================================================*/
.collapse-wrap {
    width: 100%;
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 1rem;
}

.collapse {
    padding: 1rem;
    margin-bottom: 0px;

    .collapse-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 40px;
        width: 100%;
        padding: 0 20px;
        cursor: pointer;

        h3 {
            font-size: 1rem;
            font-weight: 600;
            color: hsl(226deg 34% 24%);
        }

        .collapse-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 30px;
            width: 30px;
            background: #fff;
            border-radius: 50%;
            border: 1px solid transparent;
            transition: all 0.3s;

            span {
                display: block;
                height: 16px;
                width: 16px;
            }

            span svg {
                position: relative;
                top: -2px;
                vertical-align: initial;
                height: 16px;
                width: 16px;
                color: hsl(232deg 14% 68%);
            }
        }
    }

    .collapse-content {
        display: block;
        padding: 15px 20px 0;
        color: hsl(232deg 14% 68%);
    }

    &.has-plus[open] .collapse-content {
        display: block;
    }

    &.has-plus[open] .collapse-header .collapse-icon {
        transform: rotate(45deg);
    }

    .collapse-content p {
        color: $dark-text;
        text-align: right;
        margin-bottom: 0;
    }
}

