.box {
    position: relative;
    color: #4a4a4a;
    background: #fff;
    border-radius: 1rem;
    border: 1px solid #e5e5e5;
    box-shadow: -1px 3px 10px 0 rgb(0 0 0 / 6%);

    & {
        padding: 1.25rem;
        display: block;
        overflow: hidden;
        transition: transform 0.3s ease;
    }

    .feature-block-image {
        margin: 0px auto 0.75rem;
        height: 60px;
        width: 60px;
        transition: transform 0.3s ease 0s;
    }

    .feature-block-image.is-sm {
        height: 50px;
        width: 50px;
    }

    &:not(.bubble) {
        position: relative;
        z-index: 1;
    }

    &:not(.has-text-centered) .feature-block-image {
        margin: 0px 0px 0.75rem;
    }
}

.box.animated {
    transition: transform 0.3s, box-shadow 0.3s;
}

.box.animated:hover {
    transform: translateY(-5px);
    box-shadow: -1px 3px 10px 0 rgb(0 0 0 / 12%);

    .icon-box {
        transform: rotate(15deg);
    }
}
