.avatar {
    position: relative;
    display: inline-block;
    height: 38px;
    width: 38px;
    min-width: 38px;
    border-radius: 50%;

    .avatar-image {
        display: block;
        height: 100%;
        width: 100%;
        border-radius: inherit;
    }

    &.is-squared {
        border-radius: 0.75rem;
    }

    &.is-sm {
        height: 28px;
        width: 28px;

        .is-squared {
            border-radius: 0.75rem;
        }
    }

    &.is-md {
        height: 48px;
        width: 48px;

        .is-squared {
            border-radius: 0.85rem;
        }
    }

    &.is-lg {
        height: 62px;
        width: 62px;

        .is-squared {
            border-radius: 1rem;
        }
    }

    &.is-xl {
        height: 80px;
        width: 80px;

        .is-squared {
            border-radius: 1.25rem;
        }
    }

    &.is-xxl {
        height: 100px;
        width: 100px;

        .is-squared {
            border-radius: 1.5rem;
        }
    }
}

.avatar-group {
    display: flex;
}

.avatar-group.is-compact .avatar .avatar-image {
    border: 3px solid #ffffff;
}

[dir="ltr"] .avatar-group.is-compact *:not(:first-child) {
    margin-left: -0.75rem;
}

[dir="rtl"] .avatar-group.is-compact *:not(:first-child) {
    margin-right: -0.75rem;
}

.avatar-group .avatar-counter {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 38px;
    width: 38px;
    min-width: 38px;
    border-radius: 50%;
    background: hsl(228deg 33% 97%);
}
