.title {
    color: #283252;
    font-size: 2rem;
    font-weight: 600;
    line-height: 1.5;

    &.is-1 {
        font-size: 3rem;
    }

    &.is-2 {
        font-size: 2.5rem;
    }

    &.is-3 {
        font-size: 2rem;
    }

    &.is-4 {
        font-size: 1.5rem;
    }

    &.is-5 {
        font-size: 1.25rem;
    }

    &.is-6 {
        font-size: 1rem;
    }

    &.is-bold {
        font-weight: 700;
    }

    .is-semi {
        font-weight: 600;
    }

    &.is-thin {
        font-weight: 400;
    }

    &.is-primary {
        color: $primary;
    }

    &.is-secondary {
        color: $secondary;
    }

    &.is-success {
        color: $success;
    }

    &.is-info {
        color: $info;
    }

    &.is-success {
        color: $warning;
    }

    &.is-danger {
        color: $danger;
    }

    &.is-narrow {
        margin-bottom: 0;
    }

    &.is-leading {
        margin-bottom: 0.5rem;
    }

    &.is-inverted {
        color: #fcfcfc;
    }
}

.subtitle {
    color: $medium-text;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;

    &.is-1 {
        font-size: 2rem;
    }

    &.is-2 {
        font-size: 1.75rem;
    }

    &.is-3 {
        font-size: 1.5rem;
    }

    &.is-4 {
        font-size: 1.25rem;
    }

    &.is-5 {
        font-size: 1rem;
    }

    &.is-bold {
        font-weight: 700;
    }

    .is-semi {
        font-weight: 600;
    }

    &.is-thin {
        font-weight: 400;
    }

    &.is-primary {
        color: $primary;
    }

    &.is-secondary {
        color: $secondary;
    }

    &.is-success {
        color: $success;
    }

    &.is-info {
        color: $info;
    }

    &.is-success {
        color: $warning;
    }

    &.is-danger {
        color: $danger;
    }

    &.is-inverted {
        color: #fcfcfc;
    }
}

.title,
.subtitle {
    word-break: break-word;
}

.text-primary {
    color: $primary;
}

.action-link {
    display: inline-flex;
    align-items: center;
    margin-top: 0.5rem;
    font-size: 1rem;
    font-weight: 500;
    color: rgb(119 3 252);

    &.is-semi {
        font-weight: 600;
    }
}

.action-link .iconify {
    vertical-align: middle;
    margin-inline-start: 0.25rem;
    transition: transform 0.3s;
}

.paragraph {
    color: hsl(229deg 11% 51%);
    font-size: 1rem;

    &.is-inverted {
        color: hsl(232deg 14% 68%);
    }
}

.fs-1 {
    font-size: 2rem;
}

.fs-2 {
    font-size: 1.75rem;
}

.fs-3 {
    font-size: 1.5rem;
}

.fs-4 {
    font-size: 1.25rem;
}

.fs-5 {
    font-size: 1rem;
}

.fs-6 {
    font-size: 0.75rem;
}

.has-text-centered {
    text-align: center;
}

.iconify {
    vertical-align: middle;
}

.text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.text-right {
    text-align: right;
}

.info-block {
    display: inline-block;
    font-weight: 600;
    font-size: 0.9rem;
    color: hsl(268deg 98% 50%);
    margin: 0.5rem 0px;
}
