.button {
    text-align: center;
    background-color: transparent;
    border: 1px solid transparent;
    padding: 0.786rem 1.25rem;
    border-radius: 0.65rem;

    & {
        display: inline-block;
        font-weight: 400;
        color: #6e6b7b;
        vertical-align: middle;
        user-select: none;
        font-size: 1rem;
        line-height: 1;
    }

    & {
        transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
            box-shadow 0.15s ease-in-out, background 0s, border 0s;
    }

    &.is-md {
        padding: 0.715rem 0.736rem;
    }

    &.is-sm {
        padding: 0.315rem 0.336rem;
    }

    &.is-block {
        display: flex;
        width: 100%;
    }

    &:not(:disabled):not(.disabled) {
        cursor: pointer;
    }
}

.button {
    &.is-primary {
        color: #fff !important;

        & {
            border-color: $primary;
            background-color: $primary;
        }

        &.is-raised:hover {
            box-shadow: 0 14px 26px -12px rgb(119 3 252 / 42%), 0 4px 23px 0 rgb(0 0 0 / 12%),
                0 8px 10px -5px rgb(119 0 255 / 20%);
        }
    }

    &.is-secondary {
        color: #fff !important;

        & {
            border-color: $secondary;
            background-color: $secondary;
        }
    }

    &.is-success {
        color: #fff !important;

        & {
            border-color: $success;
            background-color: $success;
        }
    }

    &.is-warning {
        color: #fff !important;

        & {
            border-color: $warning;
            background-color: $warning;
        }
    }

    &.is-danger {
        color: #fff !important;

        & {
            border-color: $danger;
            background-color: $danger;
        }
    }

    &.is-info {
        color: #fff !important;

        & {
            border-color: $info;
            background-color: $info;
        }
    }
}

.button {
    &.is-outline-primary {
        color: $primary;

        & {
            border: 1px solid $primary;
            background-color: transparent;
        }
    }

    &.is-outline-secondary {
        color: $secondary;

        & {
            border: 1px solid $secondary;
            background-color: transparent;
        }
    }

    &.is-outline-success {
        color: $success;

        & {
            border: 1px solid $success;
            background-color: transparent;
        }
    }

    &.is-outline-warning {
        color: $warning;

        & {
            border: 1px solid $warning;
            background-color: transparent;
        }
    }

    &.is-outline-danger {
        color: $danger;

        & {
            border: 1px solid $danger;
            background-color: transparent;
        }
    }

    &.is-outline-info {
        color: $info;

        & {
            border: 1px solid $info;
            background-color: transparent;
        }
    }
}

.button {
    &.is-flat-primary {
        color: $primary;
        background-color: transparent;
    }

    &.is-flat-secondary {
        color: $secondary;
        background-color: transparent;
    }

    &.is-flat-success {
        color: $success;
        background-color: transparent;
    }

    &.is-flat-warning {
        color: $warning;
        background-color: transparent;
    }

    &.is-flat-danger {
        color: $danger;
        background-color: transparent;
    }

    &.is-flat-info {
        color: $info;
        background-color: transparent;
    }
}
