.table-container {
    overflow: auto;
    overflow-y: hidden;
    max-width: 100%;
}

.table {
    width: 100%;
    color: #363636;
    background: #fff;
    border-color: #e5e5e5;

    th {
        padding: 0.75rem;
        vertical-align: top;
        font-size: 0.85rem;
        font-weight: 500;
        text-transform: uppercase;
        color: hsl(226deg 34% 24%);
        border: 1px solid #e5e5e5;

        &:not([align]) {
            text-align: inherit;
        }
    }

    td {
        padding: 0.75rem;
        vertical-align: middle;
        border-color: #e5e5e5;

        &.is-medium {
            color: $medium-text;
        }
    }
}

.table-cell-flex {
    display: flex;
    align-items: center;

    .flex-content {
        margin-inline-start: 0.5rem;
    }
}
