.notification {
    padding: 1.25rem 2.5rem 1.25rem 1.5rem;
    position: relative;
    border-radius: 0.375em;
    background-color: #f5f5f5;

    &.is-primary {
        color: #fff;
        background-color: $primary;
    }

    &.is-secondary {
        color: #fff;
        background-color: $secondary;
    }

    &.is-info {
        color: #fff;
        background-color: $info;
    }

    &.is-success {
        color: #fff;
        background-color: $success;
    }

    &.is-warning {
        color: #fff;
        background-color: $warning;
    }

    &.is-danger {
        color: #fff;
        background-color: $danger;
    }

    &.is-link {
        color: #fff;
        background-color: #485fc7;
    }

    .is-primary.is-light {
        color: $primary;
        background-color: #effaf5;
    }

    .is-secondary.is-light {
        color: $secondary;
        background-color: #effaf5;
    }

    .is-info.is-light {
        color: $info;
        background-color: #effaf5;
    }

    .is-success.is-light {
        color: $success;
        background-color: #effaf5;
    }

    .is-warning.is-light {
        color: $warning;
        background-color: #effaf5;
    }

    .is-danger.is-light {
        color: $danger;
        background-color: #effaf5;
    }

    strong {
        color: currentColor;
    }
}
