.icon-box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    min-width: 40px;
    border-radius: 0.5rem;
    background: #ffffff;
    transition: all 0.3s;

    &.is-sm {
        height: 32px;
        width: 32px;
        min-width: 32px;
    }

    &.is-md {
        height: 50px;
        width: 50px;
        min-width: 50px;
        font-size: 1.25rem;
        border-radius: 0.75rem;
    }

    .is-rounded {
        border-radius: 50%;
    }

    &.is-primary {
        color: hsl(268deg 98% 50%);
        background: hsl(268 98% 95%);

        &.is-inverted {
            color: #fff;
            background: hsl(268deg 98% 50%);
        }
    }

    &.is-secondary {
        color: $secondary;
        background: hsl(268 98% 95%);

        &.is-inverted {
            color: #fff;
            background: $secondary;
        }
    }

    &.is-info {
        color: hsl(200deg 97% 45%);
        background: hsl(200 97% 93%);

        &.is-inverted {
            background: $info;
        }
    }

    &.is-success {
        color: $success;
        background: hsl(268 98% 95%);

        &.is-inverted {
            color: #fff;
            background: $success;
        }
    }

    &.is-green {
        color: hsl(113deg 59% 71%);
        background: hsl(113 59% 93%);

        &.is-inverted {
            background: $green;
        }
    }

    &.is-purple {
        color: hsl(261deg 32% 55%);
        background: hsl(261 32% 90%);

        &.is-inverted {
            background: $purple;
        }
    }

    .is-yellow {
        color: hsl(43deg 100% 72%);
        background: hsl(43 100% 96%);

        &.is-inverted {
            background: $yellow;
        }
    }

    .is-red {
        color: hsl(345deg 94% 57%);
        background: hsl(345 94% 97%);

        &.is-inverted {
            background: $red;
        }
    }
}

.has-text-centered .icon-box {
    margin: 0px auto 0.75rem;
}
