.banner-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 800px;
    display: flex;
    flex-wrap: wrap;
    flex: 0 1 auto;
    transform: skewY(-12deg);
    transform-origin: 0;
    z-index: 0;
    background: hsl(268deg 98% 50%);
    background: linear-gradient(-90deg, hsl(268deg 98% 50%), hsl(268deg 98% 38%));
}

.banner-back .banner-tape {
    display: flex;
    min-height: 180px;
    max-height: 180px;
    align-self: stretch;
    flex-grow: 1;
    width: 33.3333%;
    max-width: 33.3333%;
    transition: background-color 0.3s;

    &:nth-of-type(1),
    &:nth-of-type(2),
    &:nth-of-type(3) {
        align-self: flex-start;
    }

    &:nth-of-type(4),
    &:nth-of-type(5),
    &:nth-of-type(6) {
        align-self: flex-end;
    }

    &:nth-of-type(1) {
        background: hsl(268deg 98% 50%);
    }

    &:nth-of-type(2) {
        background: hsl(268deg 98% 38%);
    }

    &:nth-of-type(3) {
        background: hsl(268deg 98% 38%);
    }

    &:nth-of-type(4) {
        background: hsl(268deg 98% 35%);
    }

    &:nth-of-type(6) {
        background: hsl(268deg 98% 44%);
    }
}

.home-banner .banner-body {
    display: flex;
    align-items: center;

    & {
        flex-grow: 1;
        flex-shrink: 0;
    }

    .container {
        flex-grow: 1;
        flex-shrink: 1;
    }
}

.subscribe-compact .field {
    padding: 0.25rem;
    border-radius: 0.85rem;
    background-color: #fff;
    border: 2px solid #e5e5e5;
    transition: box-shadow 0.3s, border-color 0.3s;
}

.subscribe-compact .input {
    height: 48px !important;
    font-size: 1.1rem !important;
    border: none;
    box-shadow: none !important;
}

.subscribe-compact .button {
    height: 48px;
}

.banner-body {
    padding: 3rem 1.25rem;
}

@include breakpoint(md) {
    .banner-body {
        padding: 3rem;
    }
}

.home-banner .banner-footer {
    position: relative;
    flex-grow: 0;
    flex-shrink: 0;
    z-index: 2;
}
