.tag:not(body) {
    align-items: center;
    background-color: #f5f5f5;
    border-radius: 4px;
    color: #4a4a4a;
    display: inline-flex;
    font-size: 0.75rem;
    height: 2em;
    justify-content: center;
    line-height: 1.5;
    padding-left: 0.75em;
    padding-right: 0.75em;
    white-space: nowrap;

    & {
        display: inline-block;
        line-height: 2;
        height: 2em;
    }

    &.is-primary {
        color: #fff;
        background: $primary;

        &.is-outlined {
            color: $primary;
            border: 1px solid $primary;
            background: transparent !important;
        }
    }

    &.is-secondary {
        color: #fff;
        background: $secondary;

        &.is-outlined {
            color: $secondary;
            border: 1px solid $secondary;
            background: transparent !important;
        }
    }

    &.is-info {
        color: #fff;
        background: $info;

        &.is-outlined {
            color: $info;
            border: 1px solid $info;
            background: transparent !important;
        }
    }

    &.is-success {
        color: #fff;
        background: $success;

        &.is-outlined {
            color: $success;
            border: 1px solid $success;
            background: transparent !important;
        }
    }

    &.is-warning {
        color: #fff;
        background: $warning;

        &.is-outlined {
            color: $warning;
            border: 1px solid $warning;
            background: transparent !important;
        }
    }

    &.is-danger {
        color: #fff;
        background: $danger;

        &.is-outlined {
            color: $danger;
            border: 1px solid $danger;
            background: transparent !important;
        }
    }
}
