.field:not(:last-child) {
    margin-bottom: 0.75rem;
}

.field-label {
    display: block;
    margin-bottom: 0.5rem;
    font-size: 0.9rem;
    color: hsl(229deg 11% 51%);
}

@include breakpoint(md) {
    .field-label {
        flex-basis: 0;
        flex-grow: 1;
        flex-shrink: 0;
        text-align: right;
    }
}

.control {
    clear: both;
    font-size: 1rem;
    position: relative;
    text-align: inherit;
    box-sizing: border-box;

    &.has-icon {
        position: relative;

        .input {
            padding-right: 38px;
        }

        .form-icon {
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 40px;
            width: 40px;
            z-index: 1;
            vertical-align: middle;

            svg {
                width: 18px;
                height: 18px;
                vertical-align: middle;
            }
        }
    }
}

.input {
    outline: outline;
}

.input {
    -webkit-appearance: none;
    align-items: center;
    display: inline-flex;
    justify-content: flex-start;
    line-height: 1.5;
    padding-left: calc(0.75em - 1px);
    padding-right: calc(0.75em - 1px);
    padding-top: calc(0.5em - 1px);
    position: relative;
    vertical-align: top;
}

input::placeholder {
    color: $light-text;
}

.input {
    color: hsl(229deg 11% 45%);
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 0.65rem;
    padding-bottom: calc(0.6em - 1px);
    box-shadow: none;
    transition: border 0.3s, box-shadow 0.3s;

    &:focus {
        outline: none;
        border-color: #d4cfcf;
        box-shadow: -1px 3px 10px 0 rgb(0 0 0 / 6%);
    }

    &.is-inverted {
        color: #fcfcfc;
        background: hsl(217 24% 24%);
        border-color: transparent;
    }
}

.control.has-icon .input:focus ~ .form-icon svg {
    color: hsl(268deg 98% 50%);
}

.input,
.textarea {
    width: 100%;
    max-width: 100%;
}

.field.is-grouped {
    display: flex;
    justify-content: flex-start;
}

.field.is-grouped > .control.is-expanded {
    flex-grow: 1;
    flex-shrink: 1;
}

.field.is-grouped > .control:not(:last-child) {
    margin-bottom: 0;
    margin-right: 0.75rem;
}

.textarea {
    display: block;
    max-width: 100%;
    min-width: 100%;
    padding: calc(0.75em - 1px);
    resize: vertical;
}

.textarea {
    color: hsl(229deg 11% 51%);
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 0.65rem;
    box-shadow: none;
    transition: border 0.3s ease 0s, box-shadow 0.3s ease 0s;

    &:focus {
        outline: none;
        border-color: #d4cfcf;
        box-shadow: -1px 3px 10px 0 rgb(0 0 0 / 6%);
    }
}
