.navbar {
    position: relative;
    height: 70px;
    background-color: #ffffff;
    transition: all 0.3s ease;

    &.is-fixed {
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 30;
    }

    &.is-transparent {
        background-color: transparent;
    }

    &.is-scrolled {
        background-color: #ffffff;
        box-shadow: 0 3px 6px rgb(0 0 0 / 15%);
    }

    .navbar-container {
        display: flex;
        align-items: stretch;
        min-height: 4.5rem;
        width: 100%;
    }

    .navbar-brand {
        display: flex;
        align-items: stretch;
        flex-shrink: 0;
        min-height: 3.25rem;

        & {
            height: 70px;
        }
    }

    .navbar-item {
        display: flex;
        align-items: center;
        font-size: 1rem;
        font-weight: 600;
        color: hsl(226deg 34% 24%);
        transition: color 0.3s;
    }

    .navbar-item {
        flex-grow: 0;
        flex-shrink: 0;
    }

    .navbar-item .button {
        min-height: 48px;
        min-width: 110px;
    }

    .navbar-brand .navbar-logo {
        display: block;
        width: 100%;
        max-width: 110px;
        max-height: 38px;

        & {
            filter: brightness(0) invert(1);
        }
    }

    &.is-scrolled .navbar-brand .navbar-logo {
        filter: none;
    }

    .navbar-item .has-naver {
        position: relative;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            bottom: -4px;
            width: 100%;
            height: 3px;
            background: hsl(268deg 98% 50%);
            transform: scaleX(0);
            transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
        }
    }

    .navbar-item > .iconify {
        font-size: 1.1rem;
        vertical-align: middle;
        transition: transform 0.3s;
    }

    &.is-inverted:not(.is-scrolled) .navbar-item {
        color: #ffffffe6;
    }

    &.is-transparent a.navbar-item:hover {
        color: hsl(268deg 98% 50%);
        background-color: transparent;

        .has-naver::before {
            transform: scale(1);
            transform-origin: right left;
        }
    }
}

.navbar-menu {
    display: none;
}

.navbar-brand {
    width: 100%;
}

@include breakpoint(lg) {
    .navbar,
    .navbar-menu,
    .navbar-start,
    .navbar-end {
        display: flex;
        align-items: stretch;
    }

    .navbar-menu {
        flex-grow: 1;
        flex-shrink: 0;
    }

    .navbar-brand {
        width: auto;
    }

    .b-navbar-menu {
        display: none !important;
    }
}

.navbar-item,
.navbar-link {
    color: #4a4a4a;
    display: block;
    line-height: 1.5;
    padding: 0.5rem 0.75rem;
    position: relative;
}

.navbar .navbar-menu.is-active {
    background: #fff;
}

// Mobile Styl
@media (max-width: 1023px) {
    .navbar-menu {
        padding: 0.5rem 0;
    }

    .navbar .navbar-brand {
        padding: {
            left: 0.5rem;
            right: 0.5rem;
        }
    }

    .navbar .container-lg {
        padding: 0 !important;
        display: block !important;
    }

    .navbar-menu.is-active {
        display: block;
    }

    .b-centered-mobile {
        text-align: center;
    }

    .navbar .navbar-menu .navbar-item {
        display: block;
        margin: 0 auto;

        .button {
            display: flex;
            justify-content: center;
        }
    }
}
